.workout-app {
  min-height: 100vh;
  background-color: #f3f4f6;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card {
  background-color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 480px;
  width: 100%;
  margin: 0 auto;
}

.title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-align: center;
}

.description {
  margin-bottom: 1rem;
  color: #4b5563;
  text-align: center;
}

.section {
  margin-bottom: 1.5rem;
}

.subtitle {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
}

.icon {
  margin-right: 0.5rem;
}

.list {
  list-style-type: disc;
  padding-left: 1.5rem;
  color: #4b5563;
}

.radio-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.radio-item {
  display: flex;
  align-items: center;
}

.radio {
  margin-right: 0.5rem;
}

.radio-label {
  color: #4b5563;
}

.stats {
  color: #4b5563;
}

.button {
  width: 100%;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s;
}

.primary {
  background-color: #3b82f6;
  color: white;
}

.primary:hover {
  background-color: #2563eb;
}

.success {
  background-color: #10b981;
  color: white;
}

.success:hover {
  background-color: #059669;
}

.warning {
  background-color: #f59e0b;
  color: white;
}

.warning:hover {
  background-color: #d97706;
}

.button-group {
  display: flex;
  gap: 1rem;
}

.exercise-list {
  margin-bottom: 1.5rem;
}

.exercise-item {
  padding: 0.5rem;
  background-color: #f3f4f6;
  border-radius: 0.25rem;
  margin-bottom: 0.5rem;
}

.timer {
  text-align: center;
  margin-bottom: 1.5rem;
}

.timer-display {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
  color: #3b82f6;
}

.timer-display.rest {
  color: #f59e0b;
}

.timer-display.active {
  color: #3b82f6;
}

.timer-display.cardio {
  color: #ef4444;
}

.timer-display.cooldown {
  color: #10b981;
}

.timer-label {
  color: #6b7280;
}

.badge {
  display: inline-block;
  padding: 0.25rem 0.75rem;
  background-color: #dbeafe;
  color: #1e40af;
  border-radius: 9999px;
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  text-align: center;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.progress-container {
  margin-bottom: 1.5rem;
}

.progress-bar {
  height: 0.5rem;
  background-color: #e5e7eb;
  border-radius: 9999px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background-color: #3b82f6;
}

.progress-labels {
  display: flex;
  justify-content: space-between;
  margin-top: 0.25rem;
  font-size: 0.75rem;
  color: #6b7280;
}

.exercise-progress {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}

.progress-dot {
  height: 0.25rem;
  flex: 1;
  border-radius: 9999px;
  margin: 0 0.125rem;
}

.progress-dot.completed {
  background-color: #3b82f6;
}

.progress-dot.current {
  background-color: #93c5fd;
}

.progress-dot.resting {
  background-color: #fcd34d;
}

.progress-dot.upcoming {
  background-color: #e5e7eb;
}

.completion-badge {
  width: 6rem;
  height: 6rem;
  border-radius: 9999px;
  background-color: #d1fae5;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1rem auto;
}

.checkmark {
  width: 3rem;
  height: 3rem;
  color: #10b981;
}

.summary {
  background-color: #f3f4f6;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1.5rem;
}

.summary-list {
  color: #4b5563;
  margin-top: 0.5rem;
}

@media (max-width: 640px) {
  .card {
    padding: 1rem;
  }
  
  .timer-display {
    font-size: 2.5rem;
  }
  
  .button-group {
    flex-direction: column;
    gap: 0.5rem;
  }
}